@import "scss/vars";
@import "scss/themes";
@import "scss/nullstyle";
@import "scss/transitions";
@import "scss/fonts";

.App {
    padding-top: 150px;
    min-height: 100vh;
    background-color: var(--bgColor);
    color: var(--textGrayColor);

    @media screen and (max-width: 650px) {
        padding-top: 80px;
    }
}

body {
    background-color: var(--bgColor);
    scroll-behavior: smooth;
    user-select: none;
    cursor: none !important;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

@media screen and (max-width: 700px) {
    body {
        cursor: auto !important;
    }
}

body {
    --scrollBgColor: var(--accentColor);
    overflow-y: scroll;
}

body.overflow-hidden {
    //overflow: hidden;
    height: calc(100vh);

    --scrollBgColor: var(--bgColor);

    #root {
        height: calc(100vh);
        overflow: hidden;
    }
}

* {
    outline: none;
    border: none;
    box-sizing: border-box;
    font-family: "Roboto Condensed", sans-serif;
    letter-spacing: 1px;
}

a {
    text-decoration: none;
    color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6
{
    color: var(--accentLightColor);
    font-family: 'PPNeueMachina-Regular', serif !important;
    letter-spacing: 2px;
}

button {
    //cursor: pointer;
}

::-webkit-scrollbar {
    width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
    background: var(--bgColor);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: var(--scrollBgColor);
    opacity: .7;
    border-radius: 10px;
}

html.lenis, html.lenis body {
    height: auto;
}

.lenis.lenis-smooth {
    scroll-behavior: auto !important;
}

.lenis.lenis-smooth [data-lenis-prevent] {
    overscroll-behavior: contain;
}

.lenis.lenis-stopped {
    //overflow: hidden;
}

.lenis.lenis-scrolling iframe {
    pointer-events: none;
}
